@font-face {
  font-family: 'Roboto Regular';
  src: url('/font/Roboto-Regular.tff') format('truetype') local('Roboto-Regular');
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto Regular', sans-serif;
}

#root {
  height: 100%;
}

a {
  text-decoration: none;
  font-weight: bold;
  color: #4885ed;
}

.text {
  text-align: center;
  margin: auto 4vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.text h2 {
  font-weight: normal;
  margin: .5em auto;
  line-height: initial;
  font-size: calc(2vw + 4vh);
}

.text div {
  font-size: calc(1vw + 2vh);
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  overflow: hidden;
}



